footer{
	padding-top: 30px;
	padding-bottom: 30px;
	background-color: #222222;
}
.footer{
	margin-bottom: 10px;
			@media (min-width: 768px){
				margin-bottom: 30px;
			}
		&__row{
			margin-bottom: 30px;
			@media (min-width: 768px){
				margin-bottom: 0;
			}
		}
		&__services {
		}
		&__text {
			font-weight: 600;
			display: inline-block;
			margin-bottom: 10px;
			font-size: 18px;
			border-bottom: 1px solid #fff;
			color: @color-navigation;
				@media (min-width: 768px){
					margin-bottom: 15px;
				}
		}
		&__link {
			li{
				margin-bottom: 2px;
				@media (min-width: 768px){
					margin-bottom: 5px;
				}
			}
			a{
				font-weight: 400;
				font-size: 14px;
				color: #fff;
				text-decoration: none;
				transition: 0.3s ease-out;
					@media (min-width: 992px){
						font-size: 16px;
						&:hover, &:active, &:focus{
							color: @color-navigation;
						}
					}
			}
		}
		&__copyrighted{
			width: 100%;
			padding-top: 20px;
			padding-bottom: 20px;
			font-size: 10px;
			background-color: #000;
				@media (min-width: 768px){
					font-size: 12px;
				}
				span{
					color: #fff;
					opacity: .6;
					display: inline-block;
					padding-left: 20px;
					padding-right: 20px;
				}
				&--link{
					color: @color-link;
					opacity: .6;
					transition: 0.3s ease-out;
						&:hover, &:active, &:focus{
							opacity: 1;
						}
				}
		}
}
.social{
	&__row{
		text-align: center;
		@media (min-width: 768px){
			text-align: left;
		}
	}
	&__link{
		display: inline-block;
		margin-right: 10px;
		font-size: 24px;
		transition: 0.3s ease-out;
		@media (min-width: 992px){
			&:hover, &:active, &:focus{
				color: @color-navigation;
			}
		}
	}
}	