.contacts {
		&__info{
			font-size: 16px;
				@media (min-width: 768px){
					font-size: 20px;
				}
			&--work, &--address, &--phone{
				margin-bottom: 10px;
				padding-top: 10px;
				padding-bottom: 10px;
				border-bottom: 1px solid @color-navigation;
				text-align: left;
			}
			&--work{}
			&--address{}
			&--phone{
				@media (min-width: 768px){
					margin-bottom: 30px;
				}
			}
			&--link{
				display: block;
				padding: 5px 0;
				text-decoration: none;
				color: #000;
				transition: 0.2s ease-out;
				@media (min-width: 992px){
					&:hover{
						color: @color-navigation;
					}
				}
			}
			&--icon{
				color: @color-navigation;
				font-size: 20px;
				padding-left: 5px;
				padding-right: 5px;
				@media (min-width: 576px){
					padding-left: 30%;
					font-size: 22px;
				}
			}
		}
		&__img{
			&--hover{
				padding: 0;
				background-color: rgba(0,0,0,0.4);

			}
			img{
				display: block;
				width: 100%;
				max-height: 450px;
				transition: 0.2s ease-out;
				@media (min-width: 992px){
					// &:hover {
					// 	cursor: pointer;
					//     opacity: 0.7;
					// }
				}
			}
		}
}
.maps{
	width: 100%;
	&__contacts{
		background-color: #fff;
		padding-top: 40px;
			&--text{
				margin: 0;
				padding-left: 20px;
				font-size: 2.2rem;
				margin-bottom: 20px;
			}
	}
	&__list{
		margin: 0;
		list-style: none;
		font-size: 1.9rem;
		padding-top: 20px;
		padding-bottom: 20px;
		&--slim{
			padding-top: 10px;
			padding-left: 70px;
			margin-bottom: 20px;
		}
		&--item{
			margin-bottom: 30px;
			i{
				font-size: 2.2rem;
				margin-right: 10px;
				color: @color-navigation;
				display: inline-block;
			}
			a{
				text-decoration: none;
				color: #000;
			}
			&-slim{
				font-size: 1.8rem;
				margin-bottom: 10px;
				
			}
		}
	}
}
.yandex__map{
	min-height: 450px;
}