.card{
	padding-top: 5px;
	padding-bottom: 5px;
	padding-left: 5px;
	padding-right: 5px;
	text-align: center;
		@media (min-width: 576px){
			padding-top: 10px;
			padding-bottom: 10px;
		}

	&__link{
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		justify-content: space-between;
		background-color:rgba(255, 255, 255, 0.25);
		height: 100%;
		padding-top: 15px;
		padding-bottom: 20px;
		padding-left: 5px;
		padding-right: 5px;
		text-transform: uppercase;
		border: 1px solid #000;
		border-radius: 10px;
		text-decoration: none;
		color: #000;
		box-shadow: 0 0 15px rgba(0, 0, 0, 0.6);
			@media (min-width: 576px){
				padding-top: 20px;
				padding-bottom: 20px;
			}

			& svg{
				margin: 0 auto;
				width: 50%;
				margin-bottom: 10px;
				fill: @color-svg;
				@media (min-width: 576px){
					width: 100%;
					margin-bottom: 20px;
				}
			}
	}
	&--footer{
		display: block;
		padding-top: 10px;
		padding-bottom: 10px;
		padding-left: 5px;
		padding-right: 5px;
		transition: 0.2s ease-out;
		& svg{
			margin: 0 auto;
			width: 100%;
			fill: @color-svg;
			margin-bottom: 10px;
			transition: 0.2s ease-out;
				@media (min-width: 576px){
					margin-bottom: 20px;
				}
		}
		@media (min-width: 992px){
			&:hover, &:active, &:focus{
				background-color:rgba(0, 0, 0, 0.5);
				color: #fff;
					& svg{
						fill: @color-svg-hover;
					}
			}
		}
	}
	&__title{
		display: inline-block;
		font-size: 13px;
		font-weight: 600;
		margin-bottom: 10px;
			@media (min-width: 576px){
				margin-bottom: 20px;
				line-height: 1.5;
				font-size: 16px;
			}
	}
	&__title--small{
		font-size: 14px;
		font-weight: 600;
		margin-bottom: 10px;
	}
}