@media (min-width: 992px) {
  .aside {
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 50px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.7);
  }
}
.aside p {
  text-align: left;
  font-size: 16px;
  line-height: 1.4;
}
@media (min-width: 768px) {
  .aside p {
    line-height: 1.6;
    font-size: 16px;
  }
}
.aside__quote {
  border-left: 1px solid #000;
  padding-left: 15px;
}
.aside__quote--link {
  text-decoration: underline;
  color: #1d528f;
}
@media (min-width: 992px) {
  .aside__quote--link:hover,
  .aside__quote--link:active,
  .aside__quote--link:focus {
    color: #04a49c;
  }
}
.aside__picture {
  width: auto;
  margin-bottom: 10px;
}
.aside__picture--shadow {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.5);
}
@media (min-width: 992px) {
  .aside__picture {
    margin-bottom: 15px;
  }
}
.aside__title {
  text-align: center;
  display: inline-block;
  font-size: 22px;
}
@media (min-width: 992px) {
  .aside__title {
    font-size: 32px;
  }
}
.aside__title--smile {
  font-size: 20px;
}
@media (min-width: 992px) {
  .aside__title--smile {
    font-size: 24px;
  }
}
.aside__source {
  text-align: left;
  font-size: 14px;
  padding-top: 20px;
  margin-bottom: 10px;
}
.aside__link {
  color: #000;
  transition: 0.3s ease-out;
  margin-bottom: 10px;
}
@media (min-width: 992px) {
  .aside__link:hover,
  .aside__link:active,
  .aside__link:focus {
    color: #04a49c;
  }
}
.aside__svg {
  display: block;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;
}
td {
  width: 100%;
  padding: 15px 10px;
  border: 1px solid #000;
}
.table {
  text-align: center;
  background-color: rgba(255, 255, 255, 0.25);
}
.table__title {
  margin: 0;
  margin-bottom: 30px;
}
.table span {
  display: inline-block;
  margin-bottom: 10px;
}
.table ul {
  text-align: left;
  font-size: 14px;
}
.table ul li {
  margin-bottom: 10px;
}
.table a {
  color: #f6ae48;
  transition: 0.2s ease-out;
}
@media (min-width: 992px) {
  .table a:hover,
  .table a:active,
  .table a:focus {
    color: #fff;
    text-decoration: underline;
  }
}
.table__row {
  text-align: center;
  display: block;
  border-collapse: collapse;
  overflow: auto;
  padding-bottom: 50px;
}
.table__row th {
  width: 100%;
  padding: 15px 10px;
  font-size: 14px;
  color: #fff;
  font-weight: 500;
  background: #718F73;
  border: 1px solid #000;
}
@media (min-width: 768px) {
  .table__row th {
    font-size: 18px;
  }
}
.table__row td {
  font-size: 12px;
}
@media (min-width: 768px) {
  .table__row td {
    font-size: 16px;
  }
}
.table__work {
  text-align: left;
  padding-left: 50px;
}
th.table__work {
  padding: 0;
  background: #fff;
  color: #000;
  text-align: left;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 50px;
}
th.table__work--color {
  background-color: #f0f0f0;
}
tbody tr {
  background-color: #fff;
}
tbody tr:nth-child(2n) {
  background-color: #f0f0f0;
}
