.call{
	width: 100%;
	height: auto;
	padding-top: 30px;
	padding-bottom: 30px;
	background-color: #222222;
	background-image: url("../img/fon/fon-4-min.jpg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	color: #fff;
	// margin-bottom: 30px;
		@media (min-width: 992px){
			// margin-bottom: 50px;
			background-image: url("../img/fon/fon-4.jpeg");
		}
	&__title{
		font-size: 2.6rem;
		text-align: center;
		@media (min-width: 992px){
			margin-bottom: 30px;
			font-size: 3.5rem;
		}
	}
	&__icon{
		margin-bottom: 30px;
		a{
			display: inline-flex;
			justify-content: center;
			align-items: center;
			width: 80px;
			height: 80px;
			border: 2px solid @color-navigation;
			border-radius: 50%;
			background: #fff;
			.fab{
				font-size: 3rem;
			}
			@media (min-width: 992px){
				background: none;
			}
		}
	}
	&__phone{
			margin-bottom: 30px;
			font-size: 2.6rem;
	}
}