.popup{
	position: fixed;
	display: none;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	padding-left: 20px;
	padding-right: 20px;
		&__close{
			border: none;
			top: 15px;
			right: 10px;
			padding: 3px 7px;
			width: 30px;
			height: 30px;
			position: absolute;
			cursor: pointer;
			background-color: #ebebeb;
			&--sp::before, &--sp::after{
				position: absolute;
				left: 50%;
		    	top: -4px;
		    	transform: translate(-50%,-50%);
				content: ' ';
				height: 27px;
				width: 2px;
				background-color: #f6ae48;
			}
			&--sp::before{
				transform: rotate(45deg);
			}
			&--sp::after{
				transform: rotate(-45deg);
			}
		}
		&__success{
			margin: 0;
			color: @color-navigation;
			text-align: center;
			padding-top: 50px;
			font-size: 2rem;
		}

}  
.form__container {
	max-width: 500px;
	min-width: 300px;
	/*margin: 30px auto;*/
	padding: 20px;
	border-radius: 4px;
	background-color: #ebebeb;
	box-shadow: 0 0 3px #666;
	position: fixed;
	left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
	height: 400px;
		@media (min-width: 992px){
			min-width: 400px;
			height: auto;
		}
	/*overflow-y: scroll;*/
	/*max-height: 100%;*/
}

h2, .field-block {
	margin-bottom: 20px;
}

.field-block label, .field-block .field {
	display: block;
	width: 100%;
}

.field-block label {
	margin-bottom: 8px;
	font-weight: bold;
}

.field-block .field {
	font-size: 16px;
	padding: 8px 12px;
	line-height: 1.5;
	border-radius: 4px;
	border: 1px solid #ccc;
	box-shadow: inset 0 1px 1px rgba(0,0,0,0.075);
	transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
}

.field-block .field:focus {
  border-color: #66afe9;
  outline: 0;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, .6);
}

.field-block textarea {
	resize: vertical;
}

.check-text {
	font-size: 14px;
	vertical-align: top;
}

.button {
	cursor: pointer;
	font-size: 16px;
	padding: 6px 12px;
	margin-bottom: 5px;
	border: 1px solid @color-navigation;
	border-radius: 5px;
	background-color: #fff;
	color: @color-navigation;
	transition: all 0.3s ease;
		&:hover{
			background-color: @color-navigation;
			color: #fff;
			box-shadow: 0px 0px 7px rgba(22, 185, 154, 0.8);

		}
}

#loader {
	display: none;
}
