
.header{
	// margin-bottom: 30px;
	@media (min-width: 992px){
		// margin-bottom: 50px;
	}
	&__container{
		width: 100%;
		background-color: #000;
	}
	&__nav{
		position: relative;
		margin: 0 auto;
		background-color: #000;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		padding: 15px 0;
		color: #fff;
			&--wraper{
				text-align: center;
			}
			&--social{
				margin-bottom: 10px;
				.social__link{
					font-size: 18px;
					@media (min-width: 576px){
						font-size: 24px;
					}
				}
			}
			&--list{
				font-size: 18px;
				i{
					margin-right: 5px;
					color: @color-navigation;
				}
				a{
					color: #fff;
					transition: 0.2s ease-out;
						&:hover, &:active, &:focus{
							color: @color-navigation;
						}
				}
				@media (min-width: 576px){
					font-size: 20px;
				}
			}
			&--logo{
				font-size: 22px;
				text-decoration: none;
      			color: #fff;
				@media (min-width: 576px){
					font-size: 24px;
				}
				@media (min-width: 992px){
					font-size: 26px;
				}
			}
		@media (min-width: 992px){
			align-items: center;
			justify-content: space-between;
		}
	}
	&__main{
		position: relative;
		width: auto;
		height: auto;
		padding-top: 30px;
		padding-bottom: 30px;
		background-color: #222222;
		background-image: url("../img/fon/fon-1-min.jpg");
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		color: #fff;
			@media (min-width: 992px){
				background-image: url("../img/fon/fon-1.jpeg");
				padding-top: 60px;
				padding-bottom: 90px;
			}
			&--title{
				text-align: center;
				font-size: 32px;
				font-weight: 400;
				margin-bottom: 60px;
				text-shadow: 2px 2px 0px rgba(255, 255, 255, 0.4);
					h1{
						font-size: 32px;
						font-weight: 400;
						margin: 0;
						text-shadow: 2px 2px 0px rgba(255, 255, 255, 0.4);
					}
					@media (min-width: 576px){
						font-size: 36px;
						h1{
							font-size: 36px;
						}
					}
					@media (min-width: 768px){
						font-size: 46px;
						h1{
							font-size: 46px;
						}
					}
					@media (min-width: 992px){
						padding: 30px 0;
						font-size: 50px;
						h1{
							font-size: 50px;
						}
					}
					@media (min-width: 1200px){
						padding: 50px 0;
						font-size: 58px;
						h1{
							font-size: 58px;
						}
					}
			}
	}
}
