.middle a {
  text-decoration: none;
  font-size: 1em;
  position: relative;
  transition: all 0.6s;
}
.middle a:before {
  content: "";
  width: 0;
  height: 0.1em;
  position: absolute;
  bottom: 0;
  left: 50%;
  background: #00aeff;
  transition: all 0.3s;
}
.middle a:hover:before {
  width: 100%;
  left: 0;
  background: @color-navigation;
}
.left a {
  text-decoration: none;
  font-size: 1em;
  position: relative;
  transition: all 0.6s;
}
.left a:before {
  content: "";
  width: 0;
  height: 0.1em;
  position: absolute;
  bottom: 0;
  left: 0;
  background: #00aeff;
  transition: all 0.3s;
}
.left a:hover:before {
  width: 100%;
  left: 0;
  background: @color-navigation;
}
.right a {
  text-decoration: none;
  font-size: 1em;
  position: relative;
  transition: all 0.6s;
}
.right a:before {
  content: "";
  width: 0;
  height: 0.1em;
  position: absolute;
  bottom: 0;
  left: 100%;
  background: #00aeff;
  transition: all 0.3s;
}
.right a:hover:before {
  width: 100%;
  left: 0;
  background: @color-navigation;
}
.pass a {
