.nav{
  &__link{
    a, span{
      font-family: 'Roboto', sans-serif;
      font-size: 22px;
      font-weight: 400;
      text-decoration: none;
      display: inline-block;
      color: #fff;
      padding: 10px 20px;
      text-transform: uppercase;

        @media (min-width: 992px){
          font-size: 16px;
          padding: 5px;
      }
    }
  }
  &__item{
    position: relative;
    margin: 0;
    padding: 0;
    display: block;
    margin-bottom: 20px;
    width: 100%;

      @media (min-width: 992px){
        margin-right: 10px;
        margin-bottom: 0;
        display: inline-block;
        width: auto;
        border-bottom: none;
        text-align: right;
      }

      &:last-child{
        margin-right: 0;
      }
  }
}

// Доп. навигация
.nav__work {
  padding-top: 10px;
  padding-bottom: 20px;
  margin-bottom: 15px;
    &--link{
      text-decoration: none;
      width: 100%;
      font-size: 20px;
      display: flex;
      color: #fff;
      align-items: center;
      max-height: 100px;
      padding-left: 15%;
      margin-bottom: 5px;
      transition: 0.2s ease-out;
        @media (min-width: 576px){
          padding-left: 0;
        }
        @media (min-width: 992px){
          max-height: 80px;
          margin-top: 0;
          margin-bottom: 0;
        }
        & svg{
          margin-left: 10px;
          margin-right: 10px;
          transition: 0.2s ease-out;
        }
        @media (min-width: 992px){
          &:hover, &:active, &:focus{
            color: @color-navigation;
          }
        }
    }
     @media (min-width: 992px){
          margin-bottom: 30px;
        }
}
.activ__link{
  color: @color-navigation !important;
}
.navigation__list {
  display: none;
  list-style-type: none;
  text-align: center;
  margin: 0;
  padding: 0;
  letter-spacing: 0px;
  display: none;
  z-index: 999;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #555452;

    &--open {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
      }
    @media (min-width: 992px){
      margin: 0 auto;
      background-color: transparent;
      position: relative;
      display: block;
      float: right;
    }

  a{
    transition: 0.3s ease-out;
    @media (min-width: 992px){
      &:hover, &:active, &:focus{
        color: @color-navigation;
      }
    }
  }
  .active__link{
    color: @color-navigation;
  }
}

.navigation__toggle {
  display: block;
  font-size: 38px;
  position: absolute;
  top: 23px;
  right: 16px;
  cursor: pointer;
  z-index: 2;

    @media (min-width: 1200px){
      display: none;
    }
}
.navigation__toggle--fixed {
  position: fixed;
}


// Значок мобильной навигации

.cmn-toggle-switch {
    font-size: 0;
    // position: absolute;
    position: fixed;
    z-index: 9999;
    top: 5px;
    right: 5px;
    display: block;
    overflow: hidden;

    width: 50px;
    height: 45px;
    margin: 0;
    padding: 0;

    cursor: pointer;
    -webkit-transition: background 0.3s;
            transition: background 0.3s;
    text-indent: -9999px;

    @media (min-width: 992px){
      display: none;
    }
}


.cmn-toggle-switch:focus {
  outline: none;
}

.cmn-toggle-switch span {
  display: block;
  position: absolute;
  top: 20px;
  left: 5px;
  right: 5px;
  height: 4px;
  background: @color-navigation;
}

.cmn-toggle-switch span::before,
.cmn-toggle-switch span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: @color-navigation;
  content: "";
}

.cmn-toggle-switch span::before {
  top: -12px;
}

.cmn-toggle-switch span::after {
  bottom: -12px;
}

/**
 * Style 2
 */
.cmn-toggle-switch__htx {
  background: transparent;
}

.cmn-toggle-switch__htx span {
  -webkit-transition: background 0.3s;
          transition: background 0.3s;
}

.cmn-toggle-switch__htx span::before,
.cmn-toggle-switch__htx span::after {
  -webkit-transition-duration: 0.3s, 0.3s;
          transition-duration: 0.3s, 0.3s;
  -webkit-transition-delay: 0.3s, 0;
          transition-delay: 0.3s, 0;
}

.cmn-toggle-switch__htx span::before {
  -webkit-transition-property: top, -webkit-transform;
          transition-property: top, transform;
}

.cmn-toggle-switch__htx span::after {
  -webkit-transition-property: bottom, -webkit-transform;
          transition-property: bottom, transform;
}

/* active state*/
.cmn-toggle-switch__htx.active {
  position: fixed;
}

.cmn-toggle-switch__htx.active span {
  background: none;
}

.cmn-toggle-switch__htx.active span::before {
  top: 0;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.cmn-toggle-switch__htx.active span::after {
  bottom: 0;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.cmn-toggle-switch__htx.active span::before,
.cmn-toggle-switch__htx.active span::after {
  -webkit-transition-delay: 0, 0.3s;
          transition-delay: 0, 0.3s;
}
