@import url(../fontawesome/css/all.min.css);
html{
	font-size: 10px;
}
html, body {
	min-height: 100%;
	margin: 0;
	padding: 0;
	min-width: 320px;
    font-family: 'Exo 2.0', sans-serif;
    font-display:swap;
}

body{
	font-size: 1.6rem;
	background-attachment: fixed;
	background-image: url("../img/fon/fon-2-min-2.jpg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
		@media (min-width: 992px){
			background-image: url("../img/fon/fon-2-min.jpg");
		}
		@media (min-width: 1200px){
			background-image: url("../img/fon/fon-2.jpg");
		}
}
a{
	cursor: pointer;
	text-decoration: none;
}
.header, footer{
	// background-color: #3d3d3d;
	width: 100%;
	background-color: #000;
	color: #fff;
}
header ul, header li, footer ul, footer li{
	margin: 0;
	padding: 0;
	list-style-type: none;
}
.maps{
	padding: 0;
}
aside, article, footer, nav{
	display: block;
}