td{
	width: 100%;
	padding: 15px 10px;
	border: 1px solid #000;
}
.table{
	text-align: center;
	background-color:rgba(255, 255, 255, 0.25);
	&__title{
		margin: 0;
		margin-bottom: 30px;
	}
		span{
			display: inline-block;
			margin-bottom: 10px;
		}
		ul{
			text-align: left;
			font-size: 14px;
				li{
					margin-bottom: 10px;
				}
		}
		a{
			color: @color-text;
			transition: 0.2s ease-out;
			@media (min-width: 992px){
				&:hover, &:active, &:focus{
					color: #fff;
					text-decoration: underline;
				}
			}
		}
	&__row{
		text-align: center;
		display: block;
		border-collapse: collapse;
		overflow: auto;
		padding-bottom: 50px;
			th{
				width: 100%;
				padding: 15px 10px;
				font-size: 14px;
				color: #fff;
				font-weight: 500;
				background: #718F73;
				border: 1px solid #000;
					@media (min-width: 768px){
						font-size: 18px;
					}
			}
			td{
				font-size: 12px;
					@media (min-width: 768px){
						font-size: 16px;
					}
			}
	}
	&__work{
		text-align: left;
		padding-left: 50px;

	}

}
th.table__work{
	padding: 0;
	background: #fff;
	color: #000;
	text-align: left;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-left: 50px;
		&--color{
			background-color: @color-table;
		}
}
tbody tr{
	background-color: #fff;
	&:nth-child(2n) {
    	background-color: @color-table;
	}
}
