.news{
	&__wrap{
		padding: 0;
		border: 1px solid #000;
		margin-bottom: 20px;
		background: #fff;
			&--none{
				display: none !important;
			}

	}
	&__img{
		width: 100%;
		min-height: 130px;
		max-height: 140px;
		object-fit: cover;
		background-color: #6e6e6e;
		border-bottom: 1px solid rgba(204,204,204,.51);
	}
	&__title{
		display: inline-block;
		border-bottom: 2px solid #000;
		margin-bottom: 25px;
			@media (min-width: 992px){
				margin-bottom: 70px;
			}
			&--smile{
				text-align: center;
				font-size: 16px;
					@media (min-width: 768px){
						font-size: 17px;
						line-height: 1.3;
					}
			}
			&--stile{
				text-transform: uppercase;
			}
			&--corousel{
				text-align: center;
				font-size: 20px;
				line-height: 1.3;
					@media (min-width: 768px){
						font-size: 17px;
					}
			}
	}
	&__link{
		display: inline-block;
		padding-top: 5px;
		padding-left: 10px;
		padding-right: 10px;
		text-transform: uppercase;
		color: #000;
		transition: 0.3s ease-out;
		margin-bottom: 10px;
			@media (min-width: 768px){
				margin-bottom: 20px;
				padding-top: 10px;
			}
			@media (min-width: 992px){
				margin-bottom: 40px;
				&:hover, &:active, &:focus{
					color: @color-link;
				}
			}
			
			&--back{
				text-transform: none;
				font-size: 18px;
			}
	}

}
.aside{
	display: block;
	background-color: #fff;
		@media (min-width: 768px){
			padding-left: 15px;
			padding-right: 15px;
		}
		@media (min-width: 992px){
			padding-left: 30px;
			padding-right: 30px;
			margin-bottom: 50px;
			box-shadow: 0 0 20px rgba(0,0,0,.7);
		}
		p{
			text-align: left;
			font-size: 16px;
			line-height: 1.4;
				@media (min-width: 768px){
					line-height: 1.6;
					font-size: 16px;
				}
		}
		&__quote{
			border-left: 1px solid #000;
			padding-left: 15px;
			&--link{
				text-decoration: underline;
				color: #1d528f;
				@media (min-width: 992px){
					&:hover, &:active, &:focus{
						color: @color-link;
					}
				}
			}
		}
		&__picture{
			width: auto;
				&--shadow{
					box-shadow: 0 0 15px rgba(0,0,0,.5);
				}
			margin-bottom: 10px;
				@media (min-width: 992px){
					margin-bottom: 15px;
				}
		}
		&__title{
			text-align: center;
			display: inline-block;
			font-size: 22px;
				@media (min-width: 992px){
					font-size: 32px;
				}
				&--smile{
					font-size: 20px;
					@media (min-width: 992px){
						font-size: 24px;
					}
				}
		}
		&__source{
			text-align: left;
			font-size: 14px;
			padding-top: 20px;
			margin-bottom: 10px;
		}
		&__link{
			color: #000;
			transition: 0.3s ease-out;
			margin-bottom: 10px;
			@media (min-width: 992px){
				&:hover, &:active, &:focus{
					color: @color-link;
				}
			}

	}
}

.aside__svg{
			display: block;
			width: 100%;
			margin-top: 20px;
			margin-bottom: 20px;
			text-align: center;
		}