
.logo__text--color{
	color: @color-navigation;
}
.text__style{
	text-align: center;
	margin-bottom: 20px;
	@media (min-width: 992px){
		margin-bottom: 30px;
	}
	&--title{
		margin-top: 10px;
		margin-bottom: 15px;
		font-size: 26px;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.4);
		@media (min-width: 768px){
			margin-top: 15px;
			margin-bottom: 20px;
			font-size: 30px;
			text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.4);
		}
		@media (min-width: 992px){
			margin-top: 20px;
			margin-bottom: 25px;
			font-size: 36px;
		}
	}
}
.description{
	&__arrow{
		&::before{
			content:'>>';
			display: inline-block;
			padding-right: 5px;
		}
		&::after{
			content:'<<';
			display: inline-block;
			padding-left: 5px;
		}
	}
	&__title{
		margin: 0;
		margin-bottom: 15px;
		font-size: 2rem;
		font-weight: 600;
		text-align: center;

		@media (min-width: 768px){
			font-size: 2.2rem;
			margin-bottom: 20px;
		}
		@media (min-width: 992px){
			font-size: 2.6rem;
			margin-bottom: 30px;
		}
	}
	&__text{
		text-align: left;
		font-size: 14px;
		margin: 0;
		margin-bottom: 10px;
			&:first-letter{
				margin-left: 7px;
			}
		@media (min-width: 576px){
			font-size: 16px;
			text-align: center;
				&:first-letter{
					margin-left: 0;
				}
		}
	}
}
.text__left{
	text-align: left !important;
}
.text__right{
	text-align: right !important;
}


.worck{
	padding: 0;
	padding-top: 30px;
	text-align: center;
	background-color:rgba(255, 255, 255, 0.25);
	@media (min-width: 992px){
		padding-top: 50px;
	}
	&__text{
		padding-top: 10px;
		padding-bottom: 15px;
		@media (min-width: 768px){
			padding-left: 15px;
			padding-right: 15px;
			padding-top: 20px;
			padding-bottom: 30px;
		}
		@media (min-width: 992px){
			padding-left: 30px;
			padding-right: 30px;
			padding-bottom: 50px;
		}
	}
	&__title{
			font-size: 22px;
				@media (min-width: 992px){
					font-size: 32px;
				}
				&--smile{
					font-size: 20px;
					@media (min-width: 992px){
						font-size: 24px;
					}
				}
		}
}

.border__none{
	border-bottom: none !important;
}