.developments{
	width: 100%;
	height: auto;
	padding-top: 30px;
	padding-bottom: 30px;
	background-color: #fff;
	background-image: linear-gradient( rgba(255,255,255,0.2), rgba(255,255,255,0.2)), url("../img/fon/fon-6-min.jpg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	text-align: center;
	@media (min-width: 992px){
		padding-top: 50px;
		padding-bottom: 50px;
		background-image: linear-gradient( rgba(255,255,255,0.2), rgba(255,255,255,0.2)), url("../img/fon/fon-6.jpg");
	}
	&__row{
		padding: 10px 0;
		background-color:rgba(0, 0, 0, 0.5);
	}
	&__col{
			padding: 0;
			border: 1px solid #000;
			margin-bottom: 20px;
			background: #fff;
			// padding: 5px;
			margin: 5px;
			margin-bottom: 10px;
		@media (min-width: 992px){
			// padding: 10px;
			margin: 5px;
		}

	}
	&__img{
		display: block;
		width: 100%;
		max-height: 150px;
		object-fit: cover;
		background-color: #6e6e6e;
	}
	&__title{
		margin: 0;
		display: inline-block;
		padding-bottom: 5px;
		border-bottom: 1px solid #000;
		font-size: 2.2rem;
		margin-bottom: 30px;
	}
	&__link{
		display: inline-block;
		padding-top: 10px;
		padding-bottom: 10px;
		color: #000;
		text-align: center;
		transition: 0.3s ease-out;
			h3{
				font-size: 1.6rem;
				margin: 0;
			}
			@media (min-width: 992px){
				&:hover, &:active, &:focus{
					color: @color-navigation;
					
				}
			}
	}
}