
.logo{
	color: #fff;
	padding: 10px;
	&__row{
		margin-top: 25px;
		margin-bottom: 15px;
		display: flex;
		align-items: center;
		@media (min-width: 768px){
			margin-top: 0;
		}
	}
	&__robot{
		width: 100px;
		display:block;
		padding-right: 10px;
	}
	&__text{
		font-size: 20px;
		@media (min-width: 768px){
			font-size: 16px;
		}
	}
	& a{
		color: @color-navigation;
	}
}
a.logo{
	transition: 0.2s ease-out;
	@media (min-width: 992px){
		&:hover, &:active, &:focus{
			color: @color-navigation;
		}
	}
}
.logo__list{
	li{
		&:first-child{
			color: @color-navigation !important;
		}

	}
}