.services{
	padding-top: 30px;
	padding-bottom: 40px;
	text-align: center;
		@media (min-width: 768px){
			padding-top: 50px;
			padding-bottom: 50px;
		}
		@media (min-width: 968px){
			padding-top: 70px;
			padding-bottom: 70px;
		}
		&__row{
			margin-bottom: 20px;
			@media (min-width: 768px){
				margin-bottom: 30px;
			}
			@media (min-width: 968px){
				margin-bottom: 70px;
			}
		}
		&__text{
			margin-bottom: 20px;
			padding-left: 0;
			padding-bottom: 0;
				@media (min-width: 768px){
					margin-bottom: 30px;
				}
		}
		&__title{
			display: inline-block;
			text-align: center;
			font-size: 18px;
			font-weight: bold;
			line-height: 1;
			margin-bottom: 5px;
				@media (min-width: 768px){
					font-size: 20px;
					line-height: 1.2;
					margin-top: 5px;
					margin-bottom: 15px;
				}
				&--decor{
					text-align: left;
				}
		}
		&__list{
			text-align: left;
			letter-spacing: 0.5px;
			padding-top: 20px;
			padding-bottom: 20px;
			@media (min-width: 768px){
					letter-spacing: none;
				}
			li{
					margin-bottom: 5px;
					font-size: 14px;
						@media (min-width: 768px){
							font-size: 16px;
							margin-bottom: 10px;
						}
				}
			&--decor{
				list-style-type: none;
				font-size: 18px;
			}
		}
}
