#toTop {
	display:none;
	text-decoration:none;
	position:fixed;
	bottom: 30px;
	right:10px;
	overflow:hidden;
	width: 45px;
	height: 45px;
	border:none;
	text-indent:100%;
	// background:url(../img/upper.png) no-repeat left top;
	background:url(../img/icon/up-2.svg) no-repeat left top;
	background-position: center;
	background-size: cover;
}

#toTopHover {
	// background:url(../img/upper.png) no-repeat left -51px;
	background:url(../img/icon/up-2.svg) no-repeat left -51px;
	background-position: center;
	background-size: cover;
	width: 45px;
	height: 45px;
	display:block;
	overflow:hidden;
	float:left;
	opacity: 0;
	-moz-opacity: 0;
	filter:alpha(opacity=0);
}

#toTop:active, #toTop:focus {
	outline:none;
}