.advantages{
	padding-top: 10px;
	@media (min-width: 992px){
		padding-top: 20px;
	}
	.row{
		margin-bottom: 20px;
		border-bottom: 1px solid #ABABAB;
		@media (min-width: 992px){
			margin-bottom: 40px;
			border-bottom: none;
		}
	}
	&__item{
		padding-top: 15px;
		padding-bottom: 15px;
			&--text{
				font-size: 1.6rem;
				text-align: center;
				margin: 0;
				margin-top: 10px;
				margin-bottom: 10px;
				font-size: 1.7rem;
				line-height: 1.1;
				@media (min-width: 768px){
					font-size: 1.8rem;
					line-height: 1.2;
					text-align: left;
				}
				@media (min-width: 992px){
					line-height: 1.35;
				}
			}
			&--title{
				text-align: center;
				font-size: 2.4rem;
				@media (min-width: 768px) {
					font-size: 2.6rem;
				}
			}
		
	}
}
.text__center{
	text-align: center;
}